<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" id="ProAppQueryss"></QueryBox>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看详情-->
    <div class="extract_box" :class="{achievementAddActive:achievementAddActive}">
      <achievementDetails ref="achievementDetails" :data="achievementData" @signOut="signOut" @getTableList="getTableList"></achievementDetails>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import achievementDetails from './achievementDetails'
import {
  // 表格列表
  getListStage,
  // 审核
  pointStageExamine
} from '@/api/achievement'
export default {
  name: 'achievementTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          // fixed: 'left',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '斑块编号',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          // fixed: 'left',
          minWidth: 200,
          key: 'cd004'
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          // fixed: 'left',
          minWidth: 200,
          key: 'cl002',
          render: (h, params) => {
            if (params.row.st4ScsCl !== null && params.row.st4ScsCl.cl002 !== null) {
              return h('span', params.row.st4ScsCl.cl002)
            } else {
              return ''
            }
          }
        },
        {
          title: '经度',
          align: 'center',
          width: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'ck007'
        },
        {
          title: '纬度',
          align: 'center',
          width: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'ck008'
        },
        {
          title: '占地面积(km²)',
          align: 'center',
          width: 120,
          // ellipsis: true,
          // tooltip: true,
          key: 'ck011'
        },
        // {
        //   title: '建设单位',
        //   align: 'center',
        //   width: 200,
        //   ellipsis: true,
        //   tooltip: true,
        //   key: 'ck019'
        // },
        // {
        //   title: '处罚方式',
        //   align: 'center',
        //   width: 200,
        //   ellipsis: true,
        //   tooltip: true,
        //   key: 'ck040'
        // },
        // {
        //   title: '罚款(万元)',
        //   align: 'center',
        //   width: 120,
        //   ellipsis: true,
        //   tooltip: true,
        //   key: 'ck041'
        // },
        // {
        //   title: '整改措施',
        //   align: 'center',
        //   width: 200,
        //   ellipsis: true,
        //   tooltip: true,
        //   key: 'ck043'
        // },
        // {
        //   title: '备注',
        //   align: 'center',
        //   width: 200,
        //   ellipsis: true,
        //   tooltip: true,
        //   key: 'ck076'
        // },
        {
          title: '核查人',
          align: 'center',
          width: 150,
          key: 'uname'
        },
        {
          title: '审核状态',
          align: 'center',
          width: 120,
          key: 'ck067',
          render: (h, params) => {
            let auditStatus = params.row.ck067
            let btnType = ''
            if (auditStatus === 0) {
              auditStatus = '待审核'
              btnType = 'primary'
            } else if (auditStatus === 1) {
              auditStatus = '已审核'
              btnType = 'success'
            } else if (auditStatus === 2) {
              auditStatus = '审核不通过'
              btnType = 'error'
            }
            return h('div', [
              // 审核
              h('Tag', {
                props: {
                  color: btnType
                }
              }, auditStatus)
            ])
          }
        },
        {
          title: '核查状态',
          align: 'center',
          width: 120,
          key: 'ck088',
          render: (h, params) => {
            let auditStatus = params.row.ck088
            if (auditStatus === 0) {
              auditStatus = '未核查'
            } else if (auditStatus === 1) {
              auditStatus = '已核查'
            }
            return h('div', [
              // 审核
              h('span', {}, auditStatus)
            ])
          }
        },
        {
          title: '操作',
          key: '',
          width: 150,
          align: 'center',
          render: (h, params) => {
            // let auditStatus = params.row.ck067
            // let disaledStatus = false
            // if (auditStatus === 0 || auditStatus === null) {
            //   auditStatus = '待审核'
            //   disaledStatus = false
            // } else if (auditStatus === 1) {
            //   auditStatus = '已审核'
            //   disaledStatus = true
            // }
            return h('div', [
              // 审核
              // h('Button', {
              //   props: {
              //     type: 'primary',
              //     size: 'small',
              //     disabled: disaledStatus,
              //     icon: 'ios-funnel'
              //   },
              //   style: {
              //     marginRight: '5px'
              //   },
              //   on: {
              //     click: () => {
              //       this.$Modal.confirm({
              //         title: '确认',
              //         content: '请问您确定要审核该斑块吗？',
              //         onOk: () => {
              //           this.handleAudit(params.row.ck001, 1)
              //         },
              //         onCancel: () => {
              //         }
              //       })
              //     }
              //   }
              // }, auditStatus),
              // 查看详情
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px',
                  background: 'rgb(16,186,204)',
                  borderColor: 'rgb(16,186,204)'
                },
                on: {
                  click: () => {
                    this.achievementAddActive = true
                    this.achievementData = params.row
                  }
                }
              }, '查看详情')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      achievementAddActive: false,
      /* 查看详情数据 */
      achievementData: {}
    }
  },
  components: {
    QueryBox,
    achievementDetails
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'cd004',
          title: '斑块编号'
        },
        {
          type: 1,
          field: 'tname',
          title: '任务名称'
        },
        {
          type: 1,
          field: 'uname',
          title: '核查人'
        },
        {
          type: 2,
          field: 'ck067',
          title: '审核状态',
          data: [
            {
              value: '0',
              label: '待审核'
            },
            {
              value: '1',
              label: '已审核'
            },
            {
              value: '2',
              label: '审核不通过'
            }
          ]
        },
        {
          type: 2,
          field: 'ck088',
          title: '核查状态',
          data: [
            {
              value: '0',
              label: '未核查'
            },
            {
              value: '1',
              label: '已核查'
            }
          ]
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getListStage({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 审核问题点 */
    handleAudit (a, b) {
      pointStageExamine({
        data: {
          ck001: a,
          ck067: b
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '审核成功' })
        } else {
          this.$Notice.error({ title: '审核失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.achievementAddActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .achievementAddActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
  }
</style>
